/* eslint-disable no-confusing-arrow */
/* eslint-disable multiline-ternary */
import { Fragment, useEffect, useMemo } from "react"
import { useDispatch, useSelector } from "react-redux"
import { useTranslation } from "react-i18next"
import { Row, Col, Card, CardHeader, CardTitle, CardBody } from "reactstrap"
import StatsHorizontal from "@components/widgets/stats/StatsHorizontal"
import {
  User,
  UserPlus,
  UserCheck,
  UserX,
  List,
  Activity,
  Chrome,
  Lock,
  Edit3,
  Trash,
  PlusCircle,
  Info
} from "react-feather"
import Avatar from "@components/avatar"
import Timeline from "@components/timeline"
import AvatarGroup from "@components/avatar-group"
import {
  storeName,
  getAlwaysGrid,
  getDataList
} from "../my-info/store/my_activities"
import { Skeleton } from "@mui/material"

import chromeLogo from "@src/assets/images/icons/google-chrome.png"
import edgeLogo from "@src/assets/images/icons/internet-explorer.png"
import mozillaLogo from "@src/assets/images/icons/mozila-firefox.png"
import safariLogo from "@src/assets/images/icons/apple-safari.png"
import operaLogo from "@src/assets/images/icons/opera.png"
import otherLogo from "@src/assets/images/icons/internet.png"

import Moment from "react-moment"
import "moment-timezone"
import "moment/locale/id"
import UILoader from "@components/ui-loader"
import { NoDataDisplay, Loader } from "@src/shared/CommonComponents"

import jsonImg from "@src/assets/images/icons/json.png"
import ceo from "@src/assets/images/avatars/avatar-blank.png"
const WidgetLastActivity = () => {
  const { t } = useTranslation()
  const store = useSelector((state) => state[storeName])
  const dispatch = useDispatch()

  useEffect(() => {
    if ((store.data.length === 0 && !store.isLoading) || getAlwaysGrid) {
      dispatch(getDataList({}))
    }
  }, [])

  const RenderDetail = (rows) => {
    const row = rows.row
    if (row.id_activityCategory === 1) {
      const browserObj = {
        Chrome: {
          class: "text-primary",
          icon: Chrome,
          img: chromeLogo
        },
        ChromeMobile: {
          class: "text-primary",
          icon: Chrome,
          img: chromeLogo
        },
        Edge: {
          class: "text-primary",
          icon: Chrome,
          img: edgeLogo
        },
        Firefox: {
          class: "text-primary",
          icon: Chrome,
          img: mozillaLogo
        },
        Opera: {
          class: "text-primary",
          icon: Chrome,
          img: operaLogo
        },
        Safari: {
          class: "text-primary",
          icon: Chrome,
          img: safariLogo
        },
        Others: {
          class: "text-primary",
          icon: Chrome,
          img: otherLogo
        }
      }

      const imgOrIcon = row?.attributes?.user_agent?.family?.replace(/ /g, "")
      // console.log(imgOrIcon)
      const Icon = browserObj[imgOrIcon] ? browserObj[imgOrIcon].icon : Info
      const Img = browserObj[imgOrIcon]
        ? browserObj[imgOrIcon].img
        : browserObj["Others"].img
      return (
        <div className="d-flex align-items-center">
          <img className="me-1" src={Img} alt="browser_logo" height="23" />
          <span className="fw-bolder">
            {row?.attributes.user_agent.family} {t("on")}{" "}
            {row?.attributes.os.family} {row?.attributes.os.major} | ip:{" "}
            {row?.attributes.ip} | device: {row?.attributes.device.family}{" "}
            {row?.attributes.device.model}
          </span>
        </div>
      )
    } else {
      return (
        <div className="d-flex align-items-center">
          {/* <h6 className="mb-0">{row.title}</h6>
          <span>{row?.attributes?.description}</span> */}
        </div>
      )
    }
  }

  const RenderIcon = (rows) => {
    const row = rows.row
    if (row.id_activityCategory === 1) {
      return <Lock size={14} />
    }
    if (row.id_activityCategory === 2) {
      return <PlusCircle size={14} />
    }
    if (row.id_activityCategory === 3) {
      return <Edit3 size={14} />
    }
    if (row.id_activityCategory === 4) {
      return <Trash size={14} />
    } else {
      return <Info size={14} />
    }
  }

  const RenderColor = (row) => {
    if (row.id_activityCategory === 1) {
      return "danger"
    }
    if (row.id_activityCategory === 2) {
      return "info"
    }
    if (row.id_activityCategory === 3) {
      return "warning"
    }
    if (row.id_activityCategory === 4) {
      return "danger"
    } else {
      return "primary"
    }
  }

  const timeLineData = useMemo(
    () =>
      store.isLoading
        ? store.data.map((data) => ({
            title: <Skeleton width={100} height={50} />,
            content: <Skeleton width={200} height={50} />,
            meta: <Skeleton width={90} height={50} />,
            metaClassName: "me-1",
            customContent: <Skeleton width={200} height={50} />
          }))
        : // Moment(data.created_date).locale("id").format("DD MMM YYYY")
          store.data.map((data) => ({
            title: t(data.title),
            content: `${
              t(data.attributes?.title) ?? t(data.attributes?.description) ?? ""
            } ${
              t(data.attributes?.data?.title) ??
              t(data.attributes?.data?.name) ??
              data.attributes?.data?.code ??
              ""
            }`,
            icon: <RenderIcon row={data} />,
            color: RenderColor(data),
            meta: (
              <Moment locale="id" fromNow>
                {data.created_date}
              </Moment>
            ),
            metaClassName: "me-1",
            customContent: <RenderDetail row={data} />
          })),
    [store.isLoading, store.data]
  )

  return (
    <Card style={{ minHeight: "200px" }} className="card-user-timeline">
      <CardHeader>
        <div className="d-flex align-items-center">
          <Activity className="user-timeline-title-icon" />
          <CardTitle tag="h4">{t("My Last Activity")}</CardTitle>
        </div>
      </CardHeader>
      <UILoader blocking={store.isLoading} loader={<Loader />}>
        <CardBody>
          {store.data.length > 0 ? (
            <Timeline className="ms-50 mb-0 lastActivity" data={timeLineData} />
          ) : (
            <div style={{ textAlign: "center" }} className="align-items-center">
              <NoDataDisplay />
            </div>
          )}
        </CardBody>
      </UILoader>
    </Card>
  )
}

export default WidgetLastActivity
